@import 'element';
@import 'mixin';
@import 'room';
@import 'component';

.poker {@include position-cover(relative); @include bg-image($image-bg); overflow:hidden;}

.poker .page-area { @include position-cover(relative); overflow:hidden;}


.poker ::-moz-selection { background: transparent}
.poker ::-webkit-selection {background: transparent}
.poker ::-ms-selection {background: transparent}
.poker ::selection {background: transparent}
